<template>
  <div>
    <!-- <image-uploader
      :debug="1"
      :maxWidth="800"
      :quality="0.7"
      :autoRotate="true"
      outputFormat="blob"
      :preview="true"
      :className="['fileinput', { 'fileinput--loaded': hasImage }]"
      :capture="false"
      accept="video/*,image/*"
      doNotResize="['gif', 'svg']"
      @input="setImage"
      @onUpload="startImageResize"
    ></image-uploader> -->
    <input type="file" accept="image/*" @change="onchange" id="file-input" />

  </div>
</template>

<script>
// import ImageUploader from "vue-image-upload-resize";
import axios from "axios"
export default {
  components: {
    // ImageUploader,
  },
  methods: {
    setImage: function (file) {
      this.hasImage = true;
      this.image = file;
      console.log(file);
        let formData = new FormData();
      formData.set("file", file);
      axios
        .post("http://127.0.0.1:5000/api/upload", formData, {
          headers: {
            "content-type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log(response);
        });
    },
    onchange(e) {
      const file = e.target.files[0];
      console.log(file);
      let formData = new FormData();
      formData.set("file", file);
      axios
        .post("http://127.0.0.1:5000/api/upload", formData, {
          headers: {
            "content-type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log(response);
        });
    },
  },
};
</script>

